@import "../../common";

.cv{
  @extend .contentWidth;
  .header{
    margin-top:100px;
    text-align: center;
    @media(max-width: $mobileBreakpoint){
      margin-top:40px;
    }
    .name{
      font-size:40px;
      letter-spacing: 10px;
      @media(max-width: $mobileBreakpoint){
        font-size:30px;
        letter-spacing: 5px;
      }
    }
    .position{
      margin-top:5px;
      letter-spacing: 5px;
      @media(max-width: $mobileBreakpoint){
        letter-spacing: 3px;
      }
    }
  }
  .mainInformations{
    display:flex;
    margin-top:40px;
    @media(max-width: $mobileBreakpoint){
      flex-direction: column;
      align-items: center;
    }
    .photo{
      height:200px;
      @media(max-width: $mobileBreakpoint){
        height:100px;
      }
      img{
        height:100%;
        @media(max-width: $mobileBreakpoint){
          border-radius: 50%;
        }
      }
    }
    .right{
      margin-left:15px;
      @media(max-width: $mobileBreakpoint){
        margin-left:0;
        margin-top:15px;
      }
      .resume{
        text-align: justify;
        p{
          margin-top:10px;
          margin-bottom: 10px;
          &:first-of-type{
            margin-top:0;
          }
        }
      }
    }
  }
}
