@import "../../common";

.block{
  display:grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 10px;
  grid-row-gap: 15px;
  margin-top:15px;
  @media(max-width: $tabletBreakpoint){
    grid-template-columns: auto auto;
    justify-content: center;
  }
  .contact{
    display:flex;
    align-items: center;
    svg{
      margin-right:5px;
    }
  }
  a.contact, .phone{
    color:unset;
    text-decoration: unset;
    transition:color 0.3s ease;
    cursor:pointer;
    &:hover{
      color:$orange;
    }
  }
  .phone a{
    color:unset;
    text-decoration: unset;
  }
}
