

$primaryFont: 'Raleway', sans-serif;

$contentWidth:900px;
$minMargin:20px;
// Breakpoints
$tabletBreakpoint:990px;
$mobileBreakpoint:660px;


$orange:#ff9800;

.contentWidth{
  max-width: $contentWidth;
  margin:auto;
  @media (max-width: $contentWidth + 2 * $minMargin) {
    width: auto;
    margin: 0 $minMargin;
  }
}
